.media-list {
  padding: 0 10px;
}
.media-item {
  padding: 5px;
  border-radius: 5px;
  @include flex(center, space-between);
  &:hover {
    background-color: var(--transparent-black-1);

    .media-item__thumbnail {
      .iko-btn {
        opacity: 1;
      }
    }
  }
  &.active {
    background-color: var(--transparent-black-3);

    .media-item__thumbnail {
      .iko-btn {
        opacity: 1;
      }
    }
  }

  &__thumbnail {
    width: 40px;
    height: 40px;
    border-radius: 4px;
    overflow: hidden;
    margin-right: 10px;
    position: relative;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    .iko-btn {
      opacity: 0;
      position: absolute;
      width: 100%;
      top: 0;
      left: 0;
      height: 100%;
      padding: 10px;
      background: var(--transparent-white-3);
      text-align: center;
      color: #fff;
      font-size: 20px;
    }
  }

  &__content {
    @include flex(center, flex-start);
  }

  &__name {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    line-clamp: 1;
    -webkit-box-orient: vertical;
    font-size: 14px;
  }

  &__author {
    font-size: 12px;
    opacity: 0.6;
  }

  &__actions {
    @include flex(center, center);
  }
}
